* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  min-height: 100vh;
  background: #240b36;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.form {
  width: 20rem;
  height: 3rem;
  position: relative;
}

#search-box {
  font: inherit;
  max-width: 320px;
  width: 100%;
  height: 100%;
  color: #fff;
  background: none;
  border: 2px solid #fff;
  border-radius: .5rem;
  outline: none;
  margin-bottom: 16px;
  padding: 1.25rem;
  position: absolute;
  top: 0;
  left: 0;
}

#search-box:hover, #search-box:focus {
  border-color: #fcb6cb;
}

.input-label {
  color: #fff;
  cursor: text;
  background: #240b36;
  border-radius: 30px;
  padding: 0 .5rem;
  transition: top .2s ease-in, left .2s ease-in, font-size .2s ease-in;
  position: absolute;
  top: .8rem;
  left: 1rem;
}

.search-box:focus ~ .input-label, .search-box:not(:placeholder-shown).search-box:not(:focus) ~ .input-label {
  font-size: .8rem;
  top: -.5rem;
  left: .8rem;
}

.country-list {
  flex-direction: column;
  margin-bottom: 5px;
  padding: 10px;
  font-size: x-large;
  list-style: none;
  display: flex;
}

.countries_img {
  width: 27px;
  margin-right: 10px;
}

.country-info {
  flex-direction: column;
  justify-content: center;
  margin-bottom: 5px;
  padding: 10px;
  font-size: x-large;
  display: flex;
}

h2 > img {
  margin-right: 10px;
}

